 /* Header animation */
 #Header.Active {
	-webkit-animation: HeaderActive 3s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
	        animation: HeaderActive 3s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}

 @-webkit-keyframes HeaderActive {
    0% {
      letter-spacing: 1.5em;
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      letter-spacing: normal;
      opacity: 1;
    }
  }
  @keyframes HeaderActive {
    0% {
      letter-spacing: 1.5em;
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      letter-spacing: 0.4em;
      opacity: 1;
    }
  }
  @-webkit-keyframes HeaderActiveSmart {
    0% {
      -webkit-filter: blur(12px);
              filter: blur(12px);
      opacity: 0;
    }
    100% {
      -webkit-filter: blur(0px);
              filter: blur(0px);
      opacity: 1;
    }
  }
  @keyframes HeaderActiveSmart {
    0% {
      -webkit-filter: blur(12px);
              filter: blur(12px);
      opacity: 0;
    }
    100% {
      -webkit-filter: blur(0px);
              filter: blur(0px);
      opacity: 1;
    }
  }
  
  
