/*
* These styles creates the underlining of link
*/
.LinksB,
.Links{
	position: relative;
	z-index: 1;
	display: inline-flex;
	
	padding-left: 10px;
	padding-bottom: 5px;
	padding-right: 10px;
}
.LinksB::before,
.Links::before{
    color: white;
	content: "";
	width: 100%;
	height: 50%;
	background-image: linear-gradient(to top, #51afbb 5%, rgba(0, 0, 0, 0) 40%);
	
	position: absolute;
	left: 0;
	bottom: 2px;
	z-index: -1;
	
	will-change: width;
	transform: rotate(-3deg);
	transform-origin: left bottom
}
.LinksB:hover:hover::before,
.Links:hover:hover::before{
	width: 0;
}

.LinksB::before,
.Links::before{
	transition: width .1s ease-out;
}
.LinksB:hover::before,
.Links:hover::before{
	transition-duration: .15s;
}
.LinksB{
    color: black !important;
}
  
  #btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80px;
    cursor: pointer;
    margin-top: 10px;
    margin-left: 10px;
    animation: pulse 2s ease 0s infinite;
  }
  @keyframes pulse {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.2);
    }
    100%{
      transform: scale(1);
    }
  }


  #btn span {
    display: block;
    width: 30px;
    border-radius: 3px;
    height: 2px;
    background-color:#51afbb;
    transition: all .3s;
    position: relative;
    margin-left: 25px;
  }
#btn.active{
    margin-top: 100px;
    margin-left: 25px;
    animation: none;
}
   span + span {
    margin-top: 10px;
  }

  .active span:nth-child(1) {
    animation: ease .7s top forwards;
  }
  
  .not-active span:nth-child(1) {
    animation: ease .7s top-2 forwards;
  }
  
  .active span:nth-child(2) {
    animation: ease .7s scaled forwards;
  }
  
  .not-active span:nth-child(2) {
    animation: ease .7s scaled-2 forwards;
  }
  
  .active span:nth-child(3) {
    animation: ease .7s bottom forwards;
  }
  
  .not-active span:nth-child(3) {
    animation: ease .7s bottom-2 forwards;
  }
  
  @keyframes top {
    0% {
      top: 0;
      transform: rotate(0);
    }
    50% {
      top: 12px;
      transform: rotate(0);
    }
    100% {
      top: 12px;
      transform: rotate(45deg);
    }
  }
  
  @keyframes top-2 {
    0% {
      top: 12px;
      transform: rotate(45deg);
    }
    50% {
      top: 12px;
      transform: rotate(0deg);
    }
    100% {
      top: 0;
      transform: rotate(0deg);
    }
  }
  
  @keyframes bottom {
    0% {
      bottom: 0;
      transform: rotate(0);
    }
    50% {
      bottom: 12px;
      transform: rotate(0);
    }
    100% {
      bottom: 12px;
      transform: rotate(135deg);
    }
  }
  
  @keyframes bottom-2 {
    0% {
      bottom: 12px;
      transform: rotate(135deg);
    }
    50% {
      bottom: 12px;
      transform: rotate(0);
    }
    100% {
      bottom: 0;
      transform: rotate(0);
    }
  }
  
  @keyframes scaled {
    50% {
      transform: scale(0);
    }
    100% {
      transform: scale(0);
    }
  }
  
  @keyframes scaled-2 {
    0% {
      transform: scale(0);
    }
    50% {
      transform: scale(0);
    }
    100% {
      transform: scale(1);
    }
  }
  
